<template>
	<div class="personalData">
		<van-nav-bar title="个人资料" fixed left-text="返回" @click-left="goMy" left-arrow />
		<Mymessage>
			<div class="personal">
				<ul>
					<li>
						<div class="lefts" >头像</div>
						<div class="rights">
							<span class="tips">点击头像更改 </span>
							<span class="hands" @click="changHandPic">
								<img class="auto-img" :src="Personal.userImg" alt="">
							</span>
						</div>
					</li>
					<li>
						<div class="lefts">昵称</div>
						<div class="rights">
							<span class="userName">{{Personal.nickName}}
								<van-icon name="edit" @click="openChange"></van-icon>
							</span>
						</div>
					</li>
					<li>
						<div class="lefts">用户ID</div>
						<div class="rights">
							<span class="userID">{{Personal.userId}}</span>
						</div>
					</li>
					<li>
						<div class="lefts">绑定号码</div>
						<div class="rights">
							<span class="userID">{{Personal.phone}}</span>
						</div>
					</li>

				</ul>
			</div>
		</Mymessage>
		<van-popup v-model="isOpen" position="bottom" round closeable close-icon="close" :style="{ height: '40%' }">
			<div class="title">修改昵称</div>
				<van-field v-model="newUserName" label="昵称 :" placeholder="请输入新昵称" />
				<div style="margin: 16px;">
					<van-button round block type="info" @click="changeName">确定</van-button>
				</div>
		</van-popup>
		<!-- 修改头像 -->
			<van-popup v-model="isShow">
				<van-uploader :after-read="afterRead" />
			</van-popup>
	</div>
</template>

<script>
	import "../assets/less/PersonalData.less"
	import Mymessage from "../components/Mymessage.vue"
	export default {
		name: "PersonalData",
		data() {
			return {
				Personal: [],
				tokenString: "",
				isOpen: false,
				newUserName:"",
				//修改头像
				isShow:false
			}
		},
		components: {
			Mymessage
		},
		created() {
			this.getPersonal()
		},
		methods: {
			//上传头像按钮
			afterRead(file){
				console.log(file);
				// console.log(file.content);
				let base=file.content.split(",")[1]
				// console.log(base);
				let imgType=file.file.type.split("/")[1];
				// console.log(imgType);
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				//请求方式
				this.axios({
					method: 'POST',
					url: '/updateAvatar',
					//如果是post请求,参数需要挂载到data
					data: {
						tokenString: this.tokenString,
						imgType:imgType,
						serverBase64Img:base,
					}
				}).then(res => {
					//如果请求成功清掉提示
					this.$toast.clear();
					console.log(res);
					if (res.data.code == 'H001') {
						if (res.data.result[0] === 1) {
							this.getPersonal()
							this.$toast({
								message: res.data.msg,
								forbidClick: true,
								duration:500
							})
							//如果请求成功关闭注册弹出层
							this.isShow = false
						}
					}
				
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
			//打开修改头像窗口
			changHandPic(){
				this.isShow=!this.isShow
			},
			//打开修改昵称
			openChange() {
				this.isOpen = !this.isOpen
			},
			//修改昵称
			changeName(){
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				
				//请求方式
				this.axios({
					method: 'POST',
					url: '/updateNickName',
					//如果是post请求,参数需要挂载到data
					data: {
						tokenString: this.tokenString,
						nickName:this.newUserName
					}
				}).then(res => {
					//如果请求成功清掉提示
					this.$toast.clear();
					// console.log(res);
					if (res.data.code == 'C001') {
						if (res.data.result[0] === 1) {
							this.getPersonal()
							this.$toast({
								message: res.data.msg,
								forbidClick: true,
								duration: 500
							})
							//如果请求成功关闭注册弹出层
							this.isOpen = false
							//清除用户注册信息
							this.newUserName = ''
						}
					}
				
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
			//返回上一级
			goMy() {
				this.$router.go(-1)
			},
			//获取我的资料
			getPersonal() {
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.axios({
					method: 'GET',
					url: '/findAccountInfo',
					params: {
						tokenString: this.tokenString
					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					// console.log(res);
					if (res.data.code == 700) {
						return

					}
					if (res.data.code === 'B001') {
						this.Personal = res.data.result[0]
						// console.log(this.Personal);
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
		},

	}
</script>

<style>
</style>
